import { Archives } from '@/models/Post.class';
import { usePostStore } from '@/stores/post';
import { defineComponent, onBeforeMount, onUnmounted, ref } from 'vue';
import { useI18n } from 'vue-i18n';
import Breadcrumbs from '@/components/Breadcrumbs.vue';
import Paginator from '@/components/Paginator.vue';
import { useCommonStore } from '@/stores/common';
export default defineComponent({
  name: 'Archives',
  components: {
    Breadcrumbs,
    Paginator
  },
  setup() {
    const commonStore = useCommonStore();
    const postStore = usePostStore();
    const {
      t
    } = useI18n();
    const archives = ref(new Archives().data);
    const pagination = ref({
      pageTotal: 0,
      page: 1
    });
    const fetchData = () => {
      postStore.fetchArchives(pagination.value.page).then(data => {
        pagination.value.pageTotal = data.total;
        archives.value = data.data;
      });
      commonStore.setHeaderImage(`${require('@/assets/default-cover.jpg')}`);
    };
    const pageChangeHanlder = page => {
      pagination.value.page = page;
      window.scrollTo({
        top: 0,
        behavior: 'smooth'
      });
      fetchData();
    };
    onBeforeMount(fetchData);
    onUnmounted(() => {
      commonStore.resetHeaderImage();
    });
    return {
      pageChangeHanlder,
      pagination,
      archives,
      t
    };
  }
});